import './ContactListContainer.css';
import data from './Api'
import Chat from './Chat'
import React, { useState } from "react";
function ContactListComponent(){
      
const [chats, setChats] = useState(data.conversations);

  const [chatCss, setChatCss] = [null];

  const chatsHandler = (val) => {
    setChatCss(val);
  };

  return (
    <div className="ContactConatiner">
      {chats.map((chat) => (
        <Chat chat={chat} key={chat.id} chatsHandler={chatsHandler} />
      ))}
    </div>
  );

}

export default ContactListComponent;





