import React, { useState } from "react";

function Chat(props) {
  const [shadowval, setShadowVal] = useState(1);

  const { firstName, profilePic } = props.chat.user;
  const { message } = props.chat.chats[0];
  const { updatedAt } = props.chat;

  console.log(shadowval);

  const dateTime = () => {
    var today = new Date(updatedAt);
    var dd = String(today.getDate()).padStart(2, "0");
    var mm = String(today.getMonth() + 1).padStart(2, "0"); //January is 0!
    var yyyy = today.getFullYear();

    var time = today.toString().split(" ").slice(4, 5).join("");
    time = time.slice(0, time.lastIndexOf(":"));
    today = dd + "/" + mm + "/" + yyyy + " " + time;
    return today;
  };


  return (
    <div
      className="chat"
      onClick={() => setShadowVal(5)}
    >
      <img src={profilePic} className="profile-pic" style={{ width: "65px",height: "auto" }} alt="/" />
      <div className="chat-data">
        <div className="data" style={{ fontSize: "0.85em" }}>
          {firstName}
        </div>
        <div className="data">{message}</div>
        <div className="data" style={{ fontSize: "0.7em" }}>
          {dateTime()}
        </div>
      </div>
    </div>
  );
}

export default Chat;
